import { Box, Typography } from "@mui/material";
import { KaranbalaBannerSvg } from "../../../../assets";
import { makeStyles } from "@mui/styles";
import { ThemeOptions } from "@mui/system";

const useStyles = makeStyles((theme: ThemeOptions) => ({
    banner: {
        backgroundColor: theme?.palette?.secondary["50"],
        "& > div": {
            flexBasis: "33.33%",
        },
    },
}));

const MainBanner = () => {
    const classes = useStyles();
    return (
        <Box
            className={`${classes.banner} ${"banner"}`}
            width={"100%"}
            minHeight={"365.48px"}
            height={"auto"}
            justifyContent={"space-between"}
            display={"flex"}
            flexWrap={"wrap"}
            borderRadius={"2rem"}
        >
            <Box margin={"65px 80px"} className={` ${"bannerContent"}`}>
                <Box>
                    <Typography
                        margin={"40px 0"}
                        lineHeight={"52px"}
                        fontSize={"4.5rem"}
                        variant="subtitle1"
                        textAlign={"center"}
                    >
                            
                    </Typography>
                </Box>
                <Box>
                    <Typography variant="subtitle2" fontSize={"2rem"} textAlign={"center"}>
                                       
                                    
                                      
                                   .   
                                       
                            .
                    </Typography>
                </Box>
            </Box>

            <Box margin={"24px 0 0 80px"} className={`${"bannerSvg"}`}>
                <KaranbalaBannerSvg />
            </Box>
        </Box>
    );
};

export { MainBanner };
